.navi
{
    background: rgba(255, 255, 255, 0.1);    
    padding: 5px;
    height: 50px;
    border: 2px;
    border-radius: 15px;
    backdrop-filter: blur(8px);
    flex: inline;
}
.login-bg .navi {
  margin: 0;
}

.ele
{
    padding: 20px;
    color: black;
    font-family: 'Courier New', Courier, monospace;
    font-size: large;
}
.bt
{
    height: 40px;
    width: 80px;
    color: black;
    font-family: 'Courier New', Courier, monospace;
    font-size: large;
    border: none;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.1);    
    border-radius: 10px;
    backdrop-filter: blur(8px);
}